import React from "react"
import { StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

/*
 * This component is built using `gatsby-image` to automatically serve optimized
 * images with lazy loading and reduced file sizes. The image is loaded using a
 * `StaticQuery`, which allows us to load the image from directly within this
 * component, rather than having to pass the image data down from pages.
 *
 * For more information, see the docs:
 * - `gatsby-image`: https://gatsby.dev/gatsby-image
 * - `StaticQuery`: https://gatsby.dev/staticquery
 */

const Image = (props) => (
  <StaticQuery
    query={graphql`
      query {
        placeholderImage: file(relativePath: { eq: "gatsby-astronaut.png" }) {
          childImageSharp {
            fluid(maxWidth: 300) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        pimg1: file(relativePath: { eq: "pimg1.png" }) {
          childImageSharp {
            fluid(maxWidth: 550, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        pimg2: file(relativePath: { eq: "pimg2.png" }) {
          childImageSharp {
            fluid(maxWidth: 290, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        pimg3: file(relativePath: { eq: "pimg3.png" }) {
          childImageSharp {
            fluid(maxWidth: 620, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        aw1: file(relativePath: { eq: "aw1.png" }) {
          childImageSharp {
            fluid(maxWidth: 441, maxHeight: 516,  quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        aw2: file(relativePath: { eq: "aw2.png" }) {
          childImageSharp {
            fluid(maxWidth: 341, maxHeight: 446, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }

      }
    `}
    render={data => <Img fluid={data[props.image].childImageSharp.fluid} />}
  />
)
export default Image
