import { Link } from "gatsby";
import React from "react"
import "../../style/home/home.scss"

const Header = ({ siteTitle }) => (
  <header>
    <nav>
      <Link
        to="/"
      >
        Home
        </Link>
      <Link
        to="/works"
      >
        Art Works
        </Link>
      <Link
        to="/portfolio"
      >
        Comercial Portfolios
        </Link>
      <Link
        to="/studio"
      >
        studio
        </Link>
    </nav>
  </header>
)


export default Header
